import React from "react";

import blackShirt from '../../assets/T-shirts/black.png'
import whiteShirt from '../../assets/T-shirts/white.png'
import greyShirt from '../../assets/T-shirts/greyC.png'
import blueShirt from '../../assets/T-shirts/blue.png'
import redShirt from '../../assets/T-shirts/red.png'

import whiteHoodie from '../../assets/Hoodie/HoodieWhiteC.png'
import blackHoodie from '../../assets/Hoodie/HoodieBlackC.jpg'
import redHoodie from '../../assets/Hoodie/HoodieRed.jpg'

import blackPolo from '../../assets/Polo/blackPolo.jpg'
import bluePolo from '../../assets/Polo/bluePolo.jpg'
import greyPolo from '../../assets/Polo/greyPoloC.jpg'

const ClothesColors = ({ currentClothing, color }) => {

    if (currentClothing === 'tShirt') {
        return (
            <div className="tshirt-color">
                <div className='circle' onClick={color} id={whiteShirt} style={{ backgroundColor: 'white' }} />
                <div className='circle' onClick={color} id={greyShirt} style={{ backgroundColor: 'rgb(91, 91, 92)' }} />
                <div className='circle' onClick={color} id={blackShirt} style={{ backgroundColor: 'black' }} />
                <div className='circle' onClick={color} id={blueShirt} style={{ backgroundColor: 'rgb(88, 70, 247)' }} />
                <div className='circle' onClick={color} id={redShirt} style={{ backgroundColor: 'rgb(247, 70, 70)' }} />
            </div>
        )
    }

    if (currentClothing === 'hoodie') {
        return (
            <div className="tshirt-color">
                <div className='circle' onClick={color} id={whiteHoodie} style={{ backgroundColor: 'white' }} />
                <div className='circle' onClick={color} id={blackHoodie} style={{ backgroundColor: 'black' }} />
                <div className='circle' onClick={color} id={redHoodie} style={{ backgroundColor: 'rgb(247, 70, 70)' }} />
            </div>
        )
    }

    if (currentClothing === 'polo') {
        return (
            <div className="tshirt-color">
                <div className='circle' onClick={color} id={greyPolo} style={{ backgroundColor: 'rgb(91, 91, 92)' }} />
                <div className='circle' onClick={color} id={blackPolo} style={{ backgroundColor: 'black' }} />
                <div className='circle' onClick={color} id={bluePolo} style={{ backgroundColor: 'rgb(88, 70, 247)' }} />
            </div>
        )
    }

}

export default ClothesColors