import React from "react";

import blackHoodie from '../../assets/Hoodie/HoodieBlack.jpg'
import greyShirt from '../../assets/T-shirts/grey.png'
import greyPolo from '../../assets/Polo/greyPolo.jpg'


import diplayGreyShirt from '../../assets/T-shirts/greyC.png'
import displayGreyPolo from '../../assets/Polo/greyPoloC.jpg'
import displayBlackHoodie from '../../assets/Hoodie/HoodieBlackC.jpg' 

const CurrentClothing = ({ handlecurrentClothing }) => {

    return (
        <div>
            <img onClick={handlecurrentClothing} id='tShirt' src={greyShirt} alt={diplayGreyShirt} className='clothIcon' />
            <img onClick={handlecurrentClothing} id='polo' src={greyPolo} alt={displayGreyPolo} className='clothIcon' />
            <img onClick={handlecurrentClothing} id='hoodie' src={blackHoodie} alt={displayBlackHoodie} className='clothIcon' />
        </div>
    )
}

export default CurrentClothing