import './display.css'

import React, { useRef } from "react";
import useDraggable from "../DraggScripts/UseDraggable";

const DraggableCard = ({ children }) => {
    const cardRef = useRef(null);
    useDraggable(cardRef);

    return (
        <div ref={cardRef}>
            {children}
        </div>
    );
};

const Display = ({ display, textFormat1, textFormat2, formatImageSize}) => {
    return (
        <div className="card card-content border-0 bg-gray">
            <div className='imgTshirt text-center nonSelectable'>
                <img
                    className="img responsive clothing"
                    src={display.tshirtColor}
                    alt="TshirtImg"
                />
            </div>

            <div className="shirtText text-center">
            <DraggableCard>
                    <div className='upperText draggable'>                        
                            <p className='nonSelectable' style={{ fontSize: textFormat1, color: display.textColor1, fontFamily: display.fontFamily1 }}>{display.upperText}</p>
                    </div>
                    </DraggableCard>

                    </div>
            <div className="shirtPicture">
                    <DraggableCard>
                        <div className='imgOnshirt draggable'>
                            <img className="border-0 nonSelectable"
                                src={display.shirtImg}
                                alt="defaultImg"
                                width={formatImageSize}
                            />
                        </div>
                    </DraggableCard>
            </div>

                <div className="shirtText2 text-center">
                    <DraggableCard>
                        <div className='lowerText draggable'>
                            <p className='nonSelectable' style={{ fontSize: textFormat2, color: display.textColor2, fontFamily: display.fontFamily2 }}>{display.lowerText}</p>
                        </div>
                    </DraggableCard>
                </div>
            </div>
            )
}

            export default Display