import React from "react";


const ImageOnshirt = (props) => {

    return (
    <span >
        <div className="form-group mb-1">
        <input onChange={props.urlChange} type="text" className="form-control-file mb-2" placeholder="Transparent PNG recommended!"/>
        <button onClick={props.shirtImg} className="btn btn-primary mr-2"> Submit </button>
        <input onChange={props.shirtSize} type="range" min="10" max="300" />

    </div>
        </span>
        )
}

export default ImageOnshirt