import React from "react";


const ChangeText2 = (props) => {

    return (
        <>
            <input onChange={props.lowerText} type="text" className="form-control form-control-sm mb-2" placeholder="Text 2 (Draggable)" />
            <select onChange={props.fontFamily2} className="form-control form-control-sm mb-2">
                <option>Arial</option>
                <option>Impact</option>
                <option>Cambria</option>
                <option>Georgia</option>
                <option>Verdana</option>
                <option>Helvetica</option>
                <option>Tahoma</option>
                <option>Trebuchet MS</option>
                <option>Times New Roman</option>
                <option>Garamond </option>
            </select>
            <input onChange={props.textSize2} type="range" min="12" max="52" />
            <div className="tshirt-color pb-4">
                <div className='circle smallcirc' onClick={props.textColor2} id='White' style={{ backgroundColor: 'white' }} />
                <div className='circle smallcirc' onClick={props.textColor2} id='Grey' style={{ backgroundColor: 'rgb(91, 91, 92)' }} />
                <div className='circle smallcirc' onClick={props.textColor2} id='Black' style={{ backgroundColor: 'black' }} />
                <div className='circle smallcirc' onClick={props.textColor2} id='Blue' style={{ backgroundColor: 'rgb(88, 70, 247)' }} />
                <div className='circle smallcirc' onClick={props.textColor2} id='Purple' style={{ backgroundColor: 'rgb(204, 69, 163)' }} />
                <div className='circle smallcirc' onClick={props.textColor2} id='Red' style={{ backgroundColor: 'rgb(247, 70, 70)' }} />
                <div className='circle smallcirc' onClick={props.textColor2} id='OrangeRed' style={{ backgroundColor: 'rgb(250, 110, 59)' }} />
                <div className='circle smallcirc' onClick={props.textColor2} id='Yellow' style={{ backgroundColor: 'rgb(249, 252, 71)' }} />
                <div className='circle smallcirc' onClick={props.textColor2} id='Green' style={{ backgroundColor: 'rgb(88, 197, 60)' }} />
            </div>
        </>
    )
}

export default ChangeText2