import React, { Component } from 'react';
import Display from '../design/Display'

import CurrentClothing from "../design/design_Components/currentClothing"
import ClothesColors from "../design/design_Components/ClothesColor";
import ChangeText1 from "../design/design_Components/changeText1";
import ChangeText2 from "../design/design_Components/changeText2";
import ImageOnshirt from "../design/design_Components/ImageOnShirt";


import blackShirt from '../assets/T-shirts/black.png'


let currentURL = ''

class Dashboard extends Component {

    state = {
        currentClothing: 'tShirt',
        tshirtColor: blackShirt,

        upperText: '',
        textSize1: 38,
        fontFamily1: 'Arial',
        textColor1: 'black',

        lowerText: '',
        textSize2: 38,
        fontFamily2: 'Arial',
        textColor2: 'black',

        shirtImg: 'https://taiff.next4dev.com.br/wp-content/uploads/2020/11/style-black-new.png',

        shirtSize: 150,

        textColor: 'black'
    }

    //Current Clothes /////////////
    handleCurrentClothing = (e) => {
        this.setState({ currentClothing: e.target.id })
        this.setState({ tshirtColor: e.target.alt })
    }

    //Clothes Colors /////////////
    handleTshirtColor = (e) => {
        this.setState({ tshirtColor: e.target.id })
    }

    //Change Upper Text /////////////
    handleUpperText = (e) => {
        this.setState({ upperText: e.target.value })
    }
    handleTextSize1 = (e) => {
        this.setState({ textSize1: e.target.value })
    }
    formatText1() {
        const size = this.state.textSize1
        return parseInt(size)
    }
    handleFontFamily1 = (e) => {
        this.setState({ fontFamily1: e.target.value })
    }
    handleTextColor1 = (e) => {
        this.setState({ textColor1: e.target.id })
    }

    //Change Lower Text /////////////
    handleLowerText = (e) => {
        this.setState({ lowerText: e.target.value })
    }
    handleTextSize2 = (e) => {
        this.setState({ textSize2: e.target.value })
    }
    formatText2() {
        const size = this.state.textSize2
        return parseInt(size)
    }
    handleFontFamily2 = (e) => {
        this.setState({ fontFamily2: e.target.value })
    }
    handleTextColor2 = (e) => {
        this.setState({ textColor2: e.target.id })
    }

    //T-shirt image /////////////
    handleURLchange = (e) => {
        currentURL = e.target.value
    }
    handleShirtImg = () => {
        this.setState({ shirtImg: currentURL })
    }
    handleImageSize = (e) => {
        this.setState({ shirtSize: e.target.value })
    }
    formatImageSize = () => {
        const size = this.state.shirtSize
        return parseInt(size)
    }

    //change Text color /////////////
    handleTextColor = (e) => {
        this.setState({ textColor: e.target.id })
    }

    render() {

        return (
            <div className="container py-5">
                <div className='row'>
                    <div className='col-lg-8'>
                        <Display
                            display={this.state}
                            textFormat1={this.formatText1()}
                            textFormat2={this.formatText2()}
                            formatImageSize={this.formatImageSize()}
                        />
                    </div>

                    <div className='col-lg-4'>
                        <div className='card bg-light container shadow'>
                            <h3 className="text-center">Settings</h3>
                            <hr />
                            <CurrentClothing handlecurrentClothing={this.handleCurrentClothing} />
                            <hr />

                            <h4>Change T-Shirt Color</h4>
                            <ClothesColors currentClothing={this.state.currentClothing} color={this.handleTshirtColor} />
                            <hr />

                            <h4>Change Image (URL)</h4>
                            <ImageOnshirt
                                urlChange={this.handleURLchange}
                                shirtImg={this.handleShirtImg}
                                shirtSize={this.handleImageSize}
                            />
                            <hr />

                            <h4>Change Text</h4>
                            <ChangeText1
                                upperText={this.handleUpperText}
                                textSize1={this.handleTextSize1}
                                fontFamily1={this.handleFontFamily1}
                                textColor1={this.handleTextColor1} />
                            <hr />

                            <ChangeText2
                                lowerText={this.handleLowerText}
                                textSize2={this.handleTextSize2}
                                fontFamily2={this.handleFontFamily2}
                                textColor2={this.handleTextColor2} />
                        </div>
                    </div>
                </div>
            </div >
        )
    }
}

export default Dashboard